}

[class*="span"] {
    display: block;
    float: left;
    padding: 0 1em;
    width: 100%;
    margin: 0 0 1em 0;
}

[class*="span"] img {
    margin: 0;
    height: auto;
    padding: 0;
    width: 100%;
}

