/** Expanding Nav - Credit to Alex Cican for navigation functionality **/

/* Basic Elements */

nav {
    color: $bodycolor;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 9999;
    cursor: pointer;
    -webkit-transform: translate(0, 0);
    -moz-transform: translate(0, 0);
    -o-transform: translate(0, 0);
    transform: translate(0, 0);
    -webkit-transition: -webkit-transform .4s, height .3s, background .4s;
    -moz-transition: -moz-transform .4s, height .3s, background .4s;
    transition: transform .4s, height .3s, background .4s;
    -webkit-animation: firstFadeInAndDrop .5s;
    -moz-animation: firstFadeInAndDrop .5s;
    animation: firstFadeInAndDrop .5s;
}

nav a {
    text-decoration: none;
    border-bottom: 0px;
}


/* Animate Drop Down of Menu */

@-webkit-keyframes firstFadeInAndDrop {
    0% {
        -webkit-transform: translate(0, -54px);
    }
    100% {
        -webkit-transform: translate(0, 0);
    }
}
@-moz-keyframes firstFadeInAndDrop {
    0% {
        -moz-transform: translate(0, -54px);
    }
    100% {
        -moz-transform: translate(0, 0);
    }
}

@keyframes firstFadeInAndDrop {
    0% {
        transform: translate(0, -54px);
    }
    100% {
        transform: translate(0, 0);
    }
}
/* Attached to BODY on menu click
.no_scroll {
    overflow: hidden;
}*/

/* increases menu width & height NAV BACK COLOUR */
nav.expanded {
    width: 100%;
    height: 100%;
    position: fixed;
    cursor: default;
    background: $accentcolour;
    z-index: 997;
    overflow: scroll;
}


/* positions navigation content */

#navigation {
    -webkit-transform: translate(0, -1000px);
    -moz-transform: translate(0, -1000px);
    -o-transform: translate(0, -1000px);
    transform: translate(0, -1000px);
    opacity: 0;
    display: none;
    -webkit-transition: -webkit-transform 5s, opacity 7s;
    -moz-transition: -moz-transform 5s, opacity 7s;
    -o-transition: -o-transform 5s, opacity 7s;
    transition: transform 5s, opacity 7s;
    color: white;
    font-family: $monofont;
}

/* shows navigation */
nav.expanded #navigation {
    -webkit-transform: translate(0, 0);
    /* hardware acceleration */
    -moz-transform: translate(0, 0);
    -o-transform: translate(0, 0);
    transform: translate(0, 0);
    opacity: 1;
    display: block;
    margin: 0 auto;
    z-index: 998;
    position: relative;
    width: 100%;
}
#navigation a {
    color: white;
}
#navigation a:hover {
    color: white;
}

.navigation-content-contact a:hover:after {
    border-bottom: 2px solid white;
    content: "";
    left: 0;
    margin-top: calc(0.125em / 2 * -1);
    position: absolute;
    right: 0;
    top: 50%;
}

#navigation h2 {
    display: inline-block
}
#navigation h2:hover:after {
    content: '.';
    color: white;
}
#navigation h2:hover:after #navigation h2 a {
    text-decoration: none;
}

/* Gives padding to expanded navigation  */

.navigation-content {
    margin: 0;
    padding: $s-space;
    text-align: left;
    width: 100%;
    max-width: 35em;
}
.navigation-content p {
}
.navigation-content a {
    text-decoration: none;
    border-bottom: none;
}

.navigation-content a:hover {
    text-decoration: none;
    border-bottom: none;
    position: relative;
}

/* Bar Transition to X  */


/* Positiioning Bars  */

#nav-container {
    position: fixed;
    top: $s-space;
    right: $s-space;
    margin: 0;
    z-index: 999;
}

.toggle-icon {
    position: relative;
    display: block;
    width: 100%;
    margin: -.2em auto 0 .2em;
    cursor: pointer;
    -webkit-transition: margin 600ms;
    -moz-transition: margin 600ms;
    transition: margin 600ms;
}
/* Positiioning Bars  */

.toggle-icon .bar {
    display: block;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    width: 3.5em;
    height: .3em;
    margin: .6em;
    -webkit-transition-duration: 600ms;
    -moz-transition-duration: 600ms;
    transition-duration: 600ms;
    -webkit-transition-property: -webkit-transform, margin, border-right-color, box-shadow;
    -moz-transition-property: -moz-transform, margin, border-right-color, box-shadow;
    transition-property: transform, margin, border-right-color, box-shadow;
    background: $bodycolor;
    background: $accentcolour;
}

.toggle-icon .bar:nth-of-type(2) {
    -webkit-transition: opacity 300ms;
    -moz-transition: opacity 300ms;
    transition: opacity 300ms;
}

.pushed .bar {
    background: white;
}

.pushed .bar:nth-of-type(2) {
    opacity: 0;
}

.pushed .bar:nth-of-type(1) {
    -webkit-transform: translateY(.9em) rotate(-135deg);
    -moz-transform: translateY(.9em) rotate(-135deg);
    -ms-transform: translateY(.9em) rotate(-135deg);
    -o-transform: translateY(.9em) rotate(-135deg);
    transform: translateY(.9em) rotate(-135deg);
}

.pushed .bar:nth-of-type(3) {
    -webkit-transform: translateY(-.9em) rotate(-45deg);
    -moz-transform: translateY(-.9em) rotate(-45deg);
    -ms-transform: translateY(-.9em) rotate(-45deg);
    -o-transform: translateY(-.9em) rotate(-45deg);
    transform: translateY(-.9em) rotate(-45deg);
}


/** END -- Header **/





